.Location {
	> .inner {
		max-width: 100%;
		padding-top: 0;
		.wrap {
			height: 590px;
			display: flex;
			justify-content: space-between;
			.map {
				flex: 1;
				#map {
					width: 100%;
					height: 100%;
				}
			}

			.box {
				position: relative;
				width: 35%;
				min-width: 500px;
				height: 100%;
				display: flex;
				flex-direction: column;
				justify-content: center;
				padding: 0 5%;
				background: rgba(198, 164, 126, 0.7);
				&::after {
					content: 'LOCATION';
					display: block;
					position: absolute;
					bottom: 0;
					right: 0;
					font: 500 70px/1 'Oswald';
					color: #fff;
					line-height: 1;
					letter-spacing: 0.5em;
					opacity: 0.2;
					transform: rotate(-90deg) translate(-5%, 600%);
					transform-origin: 0 0;
				}
				dl {
					margin-bottom: 30px;
					dt {
						font-size: 18px;
						font-weight: 700;
						color: #fff;
						margin-bottom: 20px;
						letter-spacing: 1.5px;
					}
					dd {
						font-size: 16px;
						font-weight: 400;
						color: #fff;
						line-height: 1.2;
						margin-bottom: 10px;
						letter-spacing: 1.5px;
					}
				}
			}
		}
	}
}
